.portfolio {
    padding-bottom: 125px;
    width: calc(100% - 70px * 2);
}

.portfolio__title {
    font-weight: 400;
    font-size: 18px;
    line-height: 1.11;
    color: #8B8B8B;
    margin: 0 0 30px;
}

.portfolio__container {
    list-style: none;
    padding: 0;
    margin: 0;
}

.portfolio__box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #424242;
    min-height: 89px;
}

.portfolio__box:last-child {
    border-bottom: none;
}

.portfolio__website {
    font-weight: 400;
    font-size: 30px;
    line-height: 1.67;
    letter-spacing: -0.04em;
    margin: 0;
}

.portfolio__link {
    background-image: url(../../images/icon-arroww.svg);
    background-repeat: no-repeat;
    height: 20px;
    width: 20px;
    display: block;
    margin: 0;
}

.hover-link:hover {
    opacity: 0.7;
    transition: ease;
}

@media screen and (max-width: 1023px) {
    .portfolio__box {
        border-bottom: 1px solid #E6E6E6;
    }

    .portfolio {
        padding-bottom: 92px;
        width: calc(100% - 50px * 2);
    }

    .portfolio__website {
        font-size: 28px;
        line-height: 1.79;
    }

    .portfolio__title {
        margin: 0 0 25px;
    }
}

@media screen and (max-width: 767px) {
    .portfolio__box {
        border-bottom: 1px solid #E6E6E6;
    }

    .portfolio {
        padding-bottom: 100px;
        width: calc(100% - 14px * 2);
    }

    .portfolio__title {
        font-size: 14px;
        line-height: 1.43;
        margin-bottom: 20px;
    }

    .portfolio__website {
        font-size: 18px;
        line-height: 1.56;
    }

    .portfolio__box {
        min-height: 67px;
    }

    .portfolio__link {
        background-image: url(../../images/icon-arrow.svg);
        height: 12px;
        width: 12px;
    }
}
