.about-me {
    padding-top: 105px;
    padding-bottom: 100px;
    width: calc(100% - 70px * 2);
}

.about-me__container {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    margin-top: 65px;
}

.about-me__photo {
    border-radius: 10px;
    height: 327px;
    width: 270px;
    margin-left: 50px;
    object-fit: cover;
}

.about-me__description {
    display: grid;
    grid-template-columns: minmax(292px, 600px);
}

.about-me__title {
    font-size: 50px;
    line-height: 1.16;
    letter-spacing: -0.04em;
    font-weight: 400;
    margin: 0;
}

.about-me__info {
    font-weight: 500;
    font-size: 18px;
    line-height: 1.11;
    margin: 20px 0 0;
}

.about-me__text {
    font-weight: 400;
    font-size: 14px;
    line-height: 1.57;
    margin: 25px 0 100px;
}

.social-networks {
    list-style: none;
    padding: 0;
    margin: 0;
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(3, minmax(min-content, max-content));
}

.social-networks__link {
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    text-decoration: none;
    color: #fff;
    cursor: pointer;
}

@media screen and (max-width: 1023px) {
    .about-me {
        padding-top: 93px;
        padding-bottom: 94px;
        width: calc(100% - 50px * 2);
    }

    .about-me__photo {
        height: 307px;
        width: 255px;
    }

    .about-me__title {
        font-size: 40px;
        line-height: 1;
    }

    .about-me__info {
        font-size: 12px;
        line-height: 1.5;
        margin-top: 16px;
    }

    .about-me__text {
        font-size: 12px;
        line-height: 1.5;
        margin-top: 20px;
        margin-bottom: 84px;
    }
}

@media screen and (max-width: 767px) {
    .about-me {
        padding-top: 50px;
        padding-bottom: 70px;
        width: calc(100% - 14px * 2);
    }

    .about-me__container {
        border-top: 1px solid #DADADA;
        flex-direction: column;
        padding-top: 60px;
        margin-top: 0px;
        align-items: center;
    }

    .about-me__photo {
        height: 352px;
        width: 292px;
        margin: 0;
    }

    .about-me__title {
        font-size: 30px;
        line-height: 36px;
        margin-top: 40px;
    }

    .about-me__info {
        font-size: 11px;
        line-height: 1.45;
        margin-top: 19px;
    }

    .about-me__text {
        font-size: 11px;
        line-height: 1.45;
        margin-bottom: 44px;
    }

    .social-networks {
        gap: 26px;
    }
}

@media screen and (max-width: 367px) {

    .about-me__photo {
        height: 357px;
        width: 280px;
        margin: 0;
    }
}
