.movies-page {
    display: flex;
    flex-direction: column;
    width: calc(100% - 60px * 2);
    margin: auto;
}

@media screen and (max-width: 1023px) {
    .movies-page {
        width: calc(100% - 30px * 2);
    }
}

@media screen and (max-width: 767px) {
    .movies-page {
        width: calc(100% - 10px * 2);
    }
}
